import { Scene } from 'phaser';

type GameKey = 'S' | 'D' | 'F' | 'G' | 'H' | 'J' | 'K';

export class PlatformManager {
    private platforms: Phaser.GameObjects.Group;
    private activePlatforms: Map<GameKey, Phaser.GameObjects.Rectangle> = new Map();
    private platformStartPositions: Map<GameKey, number> = new Map();

    private static readonly PLATFORM_CONSTANTS = {
        WIDTH: 120,
        HEIGHT: 20,
        EXTENSION: 150,
        AHEAD_OFFSET: 20,
        CENTER_OFFSET: 80,
        CLEANUP_DISTANCE: 400,
        LEVEL_HEIGHTS: {
            'S': -80,  'D': -120, 'F': -160, 'G': -200,
            'H': -240, 'J': -280, 'K': -320
        },
        LEVEL_COLORS: {
            'S': 0xe74c3c, 'D': 0xf39c12, 'F': 0xf1c40f, 'G': 0x2ecc71,
            'H': 0x3498db, 'J': 0x9b59b6, 'K': 0xe91e63
        }
    } as const;

    constructor(private scene: Scene) {
        this.platforms = this.scene.add.group();
    }

    initialize(): void {
        const { height } = this.scene.cameras.main;
        
        // Create neutral starting platform
        const neutralPlatform = this.scene.add.rectangle(200, height - 50, 800, 25, 0x95a5a6);
        this.scene.physics.add.existing(neutralPlatform, true);
        this.platforms.add(neutralPlatform);
    }

    startNewPlatform(key: GameKey, characterX: number): void {
        // Don't create duplicate platforms for the same key
        if (this.activePlatforms.has(key)) return;
        
        const { height } = this.scene.cameras.main;
        
        // Start platform right at character position for immediate response
        this.platformStartPositions.set(key, characterX);
        const platformY = height + PlatformManager.PLATFORM_CONSTANTS.LEVEL_HEIGHTS[key];
        const platformColor = PlatformManager.PLATFORM_CONSTANTS.LEVEL_COLORS[key];
        
        // Start with minimal width, will extend as key is held
        const platform = this.scene.add.rectangle(
            characterX, 
            platformY, 
            1, // Start tiny, will grow immediately
            PlatformManager.PLATFORM_CONSTANTS.HEIGHT, 
            platformColor
        );
        
        this.scene.physics.add.existing(platform, true);
        this.platforms.add(platform);
        this.activePlatforms.set(key, platform);
    }

    extendActivePlatforms(characterX: number): void {
        // Extend all currently active platforms
        this.activePlatforms.forEach((platform, key) => {
            const startX = this.platformStartPositions.get(key);
            if (startX === undefined) return;
            
            // Platform length is purely based on how long key is held
            const rawWidth = characterX - startX;
            const newX = startX + rawWidth / 2;
            
            // No minimum width - platform can be as short as a quick tap (1px minimum to avoid rendering issues)
            platform.width = Math.max(1, rawWidth);
            platform.x = newX;
            
            // Update physics body
            const body = platform.body as Phaser.Physics.Arcade.StaticBody;
            body.setSize(platform.width, platform.height);
            body.updateFromGameObject();
        });
    }

    endPlatform(key: GameKey): void {
        this.activePlatforms.delete(key);
        this.platformStartPositions.delete(key);
    }

    endAllPlatforms(): void {
        this.activePlatforms.clear();
        this.platformStartPositions.clear();
    }

    cleanupOldPlatforms(characterX: number): void {
        this.platforms.children.entries.forEach((platform) => {
            const rect = platform as Phaser.GameObjects.Rectangle;
            if (rect.x < characterX - PlatformManager.PLATFORM_CONSTANTS.CLEANUP_DISTANCE) {
                rect.destroy();
            }
        });
    }

    getPlatformsGroup(): Phaser.GameObjects.Group {
        return this.platforms;
    }

    getActivePlatforms(): Map<GameKey, Phaser.GameObjects.Rectangle> {
        return this.activePlatforms;
    }

    hasActivePlatforms(): boolean {
        return this.activePlatforms.size > 0;
    }

    getHighestActiveKey(): GameKey | null {
        if (this.activePlatforms.size === 0) return null;
        
        // Return the highest key (K is highest, S is lowest)
        const keyOrder: GameKey[] = ['K', 'J', 'H', 'G', 'F', 'D', 'S'];
        for (const key of keyOrder) {
            if (this.activePlatforms.has(key)) {
                return key;
            }
        }
        return null;
    }
}